import React from 'react'
import {Avatar, Button, Breadcrumb, Menu, Dropdown, Icon, Spin, Tooltip, Timeline, Upload, message} from 'antd';
import {Link} from 'react-router-dom'

function getBase64(img, callback) {
    const reader = new FileReader();
    reader.addEventListener('load', () => callback(reader.result));
    reader.readAsDataURL(img);
}

function beforeUpload(file) {
    console.log(file)
    console.log(file.type)
    const isJPG = file.type === 'image/jpeg' || 'image/png'
    if (!isJPG) {
        message.error('仅支持上传JPG及PNG图片！');
    }
    const isLt2M = file.size / 1024 / 1024 < 2;
    if (!isLt2M) {
        message.error('图片大小不能超过2MB!');
    }
    return isJPG && isLt2M;
}

class Setting extends React.Component {
    constructor(props) {
        super(props)
        this.state = {}
    }

    handleChange = (info) => {
        if (info.file.status === 'uploading') {
            this.setState({loading: true});
            return;
        }
        if (info.file.status === 'done') {
            // Get this url from response in real world.
            getBase64(info.file.originFileObj, imageUrl => this.setState({
                imageUrl,
                loading: false,
            }));
        }
    }

    render() {
        const uploadButton = (
            <div>
                <Icon type={this.state.loading ? 'loading' : 'plus'}/>
                <div className="ant-upload-text">Upload</div>
            </div>
        );
        const imageUrl = this.state.imageUrl;
        return (
            <div className={'content profile'}>
                <Breadcrumb style={{marginTop: '5px'}}>
                    <Breadcrumb.Item><Link to={'/'}>Home</Link></Breadcrumb.Item>
                    <Breadcrumb.Item><Link to={'/my-summary'}>用户主页</Link></Breadcrumb.Item>
                    <Breadcrumb.Item>用户设置</Breadcrumb.Item>
                </Breadcrumb>
                <Upload
                    name="avatar"
                    listType="picture-card"
                    className="avatar-uploader"
                    showUploadList={false}
                    action="http://0.0.0.0:2000/upload-head"
                    beforeUpload={beforeUpload}
                    accept="image/png, image/jpeg, image/jpg"
                    onChange={this.handleChange}
                >
                    {imageUrl ? <img style={{width: '100px', height: '100px'}} src={imageUrl} alt="avatar"/> : uploadButton}
                </Upload>
            </div>
        )
    }
}

export default Setting